function capitalize(word) {
  return `${word.charAt(0).toUpperCase()}${word.slice(1)}`
}


function times(n, iteree) {
  let arr = []

  for (let i = 0; i < n; i++) {
    arr.push(iteree)
  }

  return arr
}



export {
  capitalize,
  times
}
