class DonationPage extends Component {
// Ctor ...
  constructor(props) {
    super(props)

    this.state = {
      activeTransaction: false,
      _id: null,
      secret: null,
      amount: 0,
      token: null,
      email: null,
      name: null,
      country: null,
      zip: null,
      idempotency_key: null,
      error: false,
      errorMessage: '',
      errorCode: null,
      errorHeader: null,
      spinner: false,
      reset: false,
      donationWidget: null,
      clouds: null,
      night: false,
      stillNight: false
    }

// Bind methods...
    this.data = this.data.bind(this)
    this.fetchSecret = this.fetchSecret.bind(this)
    this.getTransactionKey = this.getTransactionKey.bind(this)
    this.updateIntent = this.updateIntent.bind(this)
    this.setAmount = this.setAmount.bind(this)
    this.displayError = this.displayError.bind(this)
    this.toggleSpinner = this.toggleSpinner.bind(this)
    this.oc = this.oc.bind(this)
  }


// Method for displaying a spinner...
  toggleSpinner() {
    if (this.state.spinner) {
      this.setState({
        spinner: false
      })
    } else {
      this.setState({
        spinner: true
      })
    }
  }


// Method for displaying errors...
  displayError(error) {
    if (error) {
      let message = error.message
// If we have a colon, remove it...
      if (/\:/.test(message)) {
        message = message.replace(':', '')
      }
// Adjust message to presence of param....
      if (error.param) {
        message = `${message} in "${error.param}" parameter.`
      }
// Set state....
      this.setState({
        error: true,
        errorHeader: error.type? `${error.type}!` : 'GeneralError!',
        errorCode: error.code? `${error.code}` : 'Error',
        errorMessage: message
      })
    }

// Display an error to the user...
    return setErrorShow(true)
  }


// Method for confirming the donor's card details....
  async confirmPayment(stripe, data) {
    // log('DATA', 'red');dir(data)
// Start the spinner....
    this.toggleSpinner()

    let {paymentIntent, error} = await stripe.confirmCardPayment(
      this.state.client_secret,
      {
        payment_method: {
          card: {
            token: data.token
          },
          billing_details: {
            name: data.name,
            address: {
              country: 'US',
              postal_code: data.zip
            }
          }
        }
      }
    )

    if (error) {
// Kill the spinner ....
      this.toggleSpinner()

// Show the error....
      this.displayError(error)
    } else {
      if (paymentIntent.status === 'succeeded') {
/// Kill the spinner ....
        this.toggleSpinner()

// Show success modal...
        setSuccessShow(true)
      }
    }
  }


// Method for initially setting a donation amount and creating a payment intent...
  async setAmount(data, headers) {
    let result =  await fetch("/payments/secret", {
      method: "POST",
      headers: headers,
      body: JSON.stringify({
        amount: data.amount
      })
    })
    return result
  }


// Method for updating a donation amount with an existing payment intent...
  async updateIntent(data, headers) {
    let result

    if (data.isAmount) {
      result =  await fetch("/payments/secret/update_intent", {
        method: "POST",
        headers: headers,
        body: JSON.stringify({
          isAmount: true,
          intent: this.state._id,
          amount: data.amount
        })
      })
    } else {
      result =  await fetch("/payments/secret/update_intent", {
        method: "POST",
        headers: headers,
        body: JSON.stringify({
          intent: this.state._id,
          receipt_email: data.email
        })
      })
    }

// Return intent.....
    return result
  }


// Create payment intent and retrieve client secret....
  async fetchSecret(data) {
// Create headers....
    let  headers = new Headers();
    headers.append('Content-Type', 'application/json')

    let response
// Send data to payment intents api...
    if (this.state.activeTransaction) {
      response = await this.updateIntent(data, headers)
    } else {
      response = await this.setAmount(data, headers)
    }

    let json = await response.json()

// If Error.....
    if (json.error) {
// display error modal...
      this.displayError(json)

// We've obtained a secret...
    } else {
      if (this.state.activeTransaction) {
        this.setState({
          amount: data.isAmount? data.amount : this.state.amount,
          client_secret: json.client_secret
        })
      } else {
        this.setState({
          activeTransaction: true,
          _id: json.id,
          client_secret: json.client_secret,
          idempotency_key: `IdemKey_${this.getTransactionKey()}`
        })
      }
    }

    return json
  }


// Method for generating a unique v4 UUID (short format) key.....
  getTransactionKey() {
// Create uuid translator/creator...
    let translator = ShortUUID(),
// Create a uuid...
    key = translator.new()

    return key
  }


// A data hub for the donation widget....
  async data(datum, stripe) {
    let type = Object.entries(datum)[0][0],
    updated

    switch (type) {
      case 'amount':
// Merge new datum with state obj...
        this.setState(datum.amount)
        // dir(datum);dir(datum.amount);log(datum.amount.amount)
// Get secret...
        this.fetchSecret(datum.amount)
        break
      case 'error':
        // log('STATE', 'blue');dir(this.state)
        if (!this.state.error) {
// Set state...
          this.setState(datum.error)
// Display the error...
          this.displayError()
        }
        break
      case 'paymentData':
// Send datum to method for sending to server...
        let pData = datum.paymentData

// Set state and fetch client secret...
        this.setState(pData)

// Update the payment intent with the donor's email addresss......
        let json = await this.fetchSecret(pData)

        if (json.client_secret) {
          this.confirmPayment(stripe, pData)
        }

        break
      case 'hide':
        this.setState(datum.hide)
        break
// Recieve donation widget ref, and store in state obj...
      case 'ref':
        this.setState(datum.ref)
        break
      case 'stillNight':
        this.setState(datum.stillNight)
        break
    }
  }

  oc() {
    this.data({
      hide: {
        hide: true,
        night: true,
        stillNight: true
      }}
    )
  }


// Page markup...
  render() {
    // log('render', 'green');dir(this.state)
    return (
      <React.Fragment>
        <Theme night={this.state.night} stillNight={this.state.stillNight} data={this.data}/>
        <DonationPageStyler>
          <Globals/>
          <StripeProvider apiKey='pk_test_oCqrBp4KQ7j6Qz7Qf38NVYT200QL5iBLNX'>
            <Container className='donation-form' fluid>
              <Row className='header-text'>
                <h1 onClick={this.oc}>Together, we can make a new day.</h1>
                <h3>Contribute to Trees for Tigers!</h3>
              </Row>
              <Row className="donation-form-row">
                <Col className="donation-form-col" xs={12} sm={8} md={6}>
                  <Elements>
                    <DonationForm
                      amount={this.state.amount}
                      reset={this.state.reset}
                      data={this.data}
                      clouds={this.state.clouds}
                    />
                  </Elements>
                </Col>
              </Row>
            </Container>
          </StripeProvider>
          <Errors
            head={this.state.errorHeader? this.state.errorHeader : null}
            code={this.state.errorCode? this.state.errorCode : null}
            error={this.state.error? this.state.errorMessage : null}
            data={this.data}
          />
          <Success widget={this.state.donationWidget} data={this.data}/>
          {
            this.state.spinner && <GridSpinner fader='fade'/>
          }
        </DonationPageStyler>
      </React.Fragment>
    )
  }
}


//
// reactDOM.render(<DonationPage/>, el('#root'))
function renderPage() {
  return reactDOM.render(<DonationPage/>, el('#root'))
}

go(()=> {
  renderPage()
})





