// Get react...
import React, {Component} from 'react'
// Get react-bootstrap lib...
// Get styled-components...
import styled from 'styled-components'

import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import Container from 'react-bootstrap/Container'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'

import {log, dir} from '../../utils/Loggers'
import {capitalize} from '../../utils/utils'


let modalShow, setModalShow


const modalBodyStyles = {
  display: 'flex',
  justifyContent: 'center',
  background: '#dc3645',
  padding: '1rem',
  border: '1px solid white',
  borderRight: 'none',
  borderLeft: 'none'
}

const modalHeaderStyles = {
  display: 'inline',
  fontSize: '1.5rem',
  color: 'white',
  marginBottom: '0',
  border: 'none'
}

const modalCodeStyles = {
  fontSize: '1.2rem',
  fontFamily: 'coder',
  color: 'white',
  borderRadius: '20px',
  padding: '.5rem',
  marginBottom: '0'
}

const modalFooterStyles = {
  padding: '1rem'
}

const modalButtonStyles = {
  display: 'flex',
  justifyContent: 'flex-end'
}

const modalRowStyles = {
  display: 'flex',
  justifyContent: 'space-around'
}

const modalMessageStyles = {
  display: 'flex',
  justifyContent: 'center'
}

const modalMessagePStyles = {
  display: 'flex',
  alignItems: 'flex-end',
  color: '#ffffff',
  marginBottom: '0',
}

const buttonStyles = {
  maxHeight: '5rem'
}

const ErrorHeader = styled.div`
  display: flex;
  align-items: flex-end;
  width: 100%;
`

// Img component...
const Cancel = styled.img.attrs({
  src: '/images/svg/misc/001-cancel.svg'
})`
  display: inline;
  height: 2.4rem;
  width: 2.4rem;
  margin-right: 1.5rem;
`


function ErrorModal(props) {
  let message, declinedRE = /declined/,
  declined = declinedRE.test(props.code)

// Prompt user to try again if payment fails....
  if (declined) {
    message = `${props.error} Please try again with another payment method.`
  } else {
    message = props.error
  }

  return (
    <Modal
      {...props}
      size="md"
      aria-labelledby="error-modal-title"
      centered
    >
      <Modal.Header className='header'>
        <Modal.Title id="error-modal-title">
          {
            props.head?
              <ErrorHeader>
                <Cancel/> <h3 style={modalHeaderStyles}>{`Error: ${props.head}!`}</h3>
              </ErrorHeader>
            :
              <ErrorHeader>
                <Cancel/> <h3 style={modalHeaderStyles}>{`Error!`}</h3>
              </ErrorHeader>
          }
        </Modal.Title>
      </Modal.Header>
      <section style={modalBodyStyles}>
        {
          props.code?
            <h5 style={modalCodeStyles}>{`( ${props.code} )`}</h5>
          :
            <p style={modalCodeStyles}>
              {props.error}
            </p>
        }
      </section>
      <Container style={modalFooterStyles} className="footer">
        <Row style={modalRowStyles} className="message-row">
          {
            props.code?
              <Col style={modalMessageStyles} className="message-col" xs={10}>
                <p style={modalMessagePStyles}>
                  {message}
                </p>
              </Col>
            :
              null
          }
          <Col style={modalButtonStyles} className="button-col" xs={2}>
            <Button style={buttonStyles} variant='danger' onClick={()=> props.onHide(declined)}>Close</Button>
          </Col>
        </Row>
      </Container>
    </Modal>
  )
}

export default function Errors(props) {
  [modalShow, setModalShow] = React.useState(false)

  let header

  function createErrorHeader(head) {
// split words of error into a list...
    let sep = head.split('_')

// use cap funtion and rejoin....
    return sep.map(word=> capitalize(word)).join('')
  }

  if (props.error) {
    log(props.error, 'red')

    if (props.head) {
      header = createErrorHeader(props.head)
    }
  }

  function hideError(declined=false) {
    setModalShow(false)

    if (declined) {
      props.data({
        hide: {
          reset: true,
          error: false
        }
      })
    } else {
      props.data({
        hide: {
          reset: false,
          error: false
        }
      })
    }
  }

  return (
    <ErrorModal
      head={header}
      error={props.error}
      code={props.code}
      show={modalShow}
      onHide={hideError}
      className='errorModal'
      data={props.data}
    />
  )
}


export {
  setModalShow
}
